.section {
  background: linear-gradient(180deg, #fff, #ffe9f1);
  padding: 72px 20px;
  position: relative;
}

.inner {
  max-width: 1100px;
  margin: 0 auto;
  position: relative;
}

.title {
  color: #e75480;
  font-family: var(--font-playfair, serif);
  text-align: center;
  margin-bottom: 40px;
  font-size: 2rem;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  letter-spacing: 0.5px;
}

.grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 28px;
}

.cardImg {
  border-radius: 20px;
  background: #fa8cde; /* base clara */
  padding: 16px;
  position: relative;
  overflow: hidden;
  box-shadow: 0 18px 40px rgba(231, 84, 128, 0.08), 
              0 6px 20px rgba(255, 182, 208, 0.2);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

/* detalhe decorativo no topo do card */
.cardImg::before {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  width: 60px;
  height: 4px;
  background: linear-gradient(90deg, #ff9cc1, #e75480, #ff9cc1);
  border-radius: 2px;
  transform: translateX(-50%);
  opacity: 0.8;
}

.cardImg:hover {
  transform: translateY(-6px) scale(1.02);
  box-shadow: 0 24px 50px rgba(231, 84, 128, 0.12),
              0 10px 30px rgba(255, 182, 208, 0.25);
}

.butterfly {
  position: absolute;
  top: -20px;
  left: 24px;
}

/* Fly1 flutuante */
.aboutDecorative {
  position: absolute;
  bottom: 90%; 
  right: 100%; 
  width: 300px;
  height: 300px;
  pointer-events: none;
  z-index: 0;
}

.overlayWrap {
  position: relative;
  width: 100%;
  height: 100%;
}

.overlayImg {
  width: 100%;
  height: 100%;
  object-fit: contain;
}


@media (min-width: 768px) {
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
